require('normalize.css');
require('styles/App.css');

import React from 'react'
import CoverCard from './CoverCard'
import ChoiceCard from './ChoiceCard'
import ResultCard from './ResultCard'
import quizData from './quizData'
import memory from './Tracker'



let AppComponent = React.createClass({

	componentWillMount () {
		memory.requestUUID()
		memory.sendMessage('initiumusersurvey_separate', 'stage', 'start', '')
		memory.sendMessage('initiumusersurvey_separate', 'userAgent', window.navigator.userAgent, '')
		memory.sendMessage('initiumusersurvey_separate', 'url', window.location.href, '')
		memory.sendMessage('initiumusersurvey_separate', 'referrer', window.document.referrer, '')
	},

	getInitialState: () => ({
		stage: 'cover',
		questionNumber: 0
	}),

	nextClickHandler () {
		let currentNumber = this.state.questionNumber
		let totalQuestionNumber = quizData.length
		let nextStage = 'qa1'

		if (currentNumber >= 3 && currentNumber <= 7){
			nextStage = 'qa2'
		} else if (currentNumber > 7 && currentNumber <= 15) {
			nextStage = 'qa3'
		} else if (currentNumber + 1 === totalQuestionNumber){
			nextStage = 'result'
		}

		this.setState({
	      questionNumber: currentNumber + 1,
	      stage: nextStage
	    })
	},

	prevClickHandler () {
		let currentNumber = this.state.questionNumber
		let prevStage = 'qa1'

		if (currentNumber === 0){
			currentNumber = 1
		} else if (currentNumber >= 5 && currentNumber <= 9){
			prevStage = 'qa2'
		} else if (currentNumber > 9 && currentNumber <= 16) {
			prevStage = 'qa3'
		}

		this.setState({
	      questionNumber: currentNumber - 1,
	      stage: prevStage
	    })
	},

	startQASection () {
		this.setState({
			stage: 'qa1',
			questionNumber: 0
		})
	},

	render() {
		let questionNumber = this.state.questionNumber
	    let currentQA = quizData[questionNumber]
	    let totalQuestionNumber = quizData.length
	    let answers = [{"UA" : window.navigator.userAgent}]
		if (this.state.stage === 'cover') {
      		return <CoverCard coverClickHandler = {this.startQASection}/>
	    } else if ((this.state.stage === 'qa1') || (this.state.stage === 'qa2') || (this.state.stage === 'qa3')){
	    	return <ChoiceCard currentQA = {currentQA}
	    					   nextClickHandler = {this.nextClickHandler}
	    					   prevClickHandler = {this.prevClickHandler}
	    					   questionNumber={questionNumber}
	    					   totalQuestionNumber={totalQuestionNumber}
	    					   state={this.state.stage}
	    					   answers={answers}/>
		} else {
      		return <ResultCard />
    	}
	}
})

export default AppComponent;



