require('normalize.css');
require('styles/App.css');

import React from 'react'
import memory from './Tracker'

let OptionText = React.createClass({

  inputChange: function(event){
    if (event.target.name === 'other') {
      this.setState({
        value: event.target.value
      })
    }
  },

  handleClickNext: function(e) {
    let answers = []
    if(e.target.className === 'nextButton') {
      var answerList = this.state.selected
      var answerContent = []
      for (var i=0; i<answerList.length; i++) {
        answerContent.push(String.fromCharCode(97 + answerList[i]).toUpperCase())
      }
      if (this.state.value !== '') {
        answerContent.push(this.state.value)
      }
      //console.log(answerList)
      
      memory.sendMessage('initiumusersurvey_separate', 'Question_' + this.props.questionNumber, 'Answer_' + answerContent)
      if (this.props.questionNumber !== this.props.totalQuestionNumber) {
        this.setState({
          selected:[],
          highlight: [],
          currentQuestion: this.props.questionNumber,
          value: ''
        })
        if (this.props.questionNumber === this.props.totalQuestionNumber - 1) {
        }
      }
      this.state.answers.push({'Question': this.props.questionNumber, 'Answer' : answerContent})
    }
  },

  addFinalAnswer: function () {
    var answerList = this.state.selected
    var answerContent = []
    for (var i=0; i<answerList.length; i++) {
      answerContent.push(String.fromCharCode(97 + answerList[i]).toUpperCase())
    }
    if (this.state.value !== '') {
      answerContent.push(this.state.value)
    }
    this.state.answers.push({'Question': this.props.totalQuestionNumber, 'Answer' : answerContent})
    console.log(JSON.stringify(this.state.answers))
    memory.sendMessage('initiumusersurvey_whole', 'Answers', JSON.stringify(this.state.answers), '')
  },

  componentDidMount: function() {
    window.addEventListener('click', this.handleClickNext);
  },

  componentWillUnmount: function() {
    window.removeEventListener('click', this.handleClickNext);
    this.addFinalAnswer()
  },

  getInitialState: function () {
      return {
        currentQuestion: this.props.questionNumber,
        selected: [],
        highlight: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' '],
        value: '',
        answers: this.props.answers
      }
  },

  change: function (idx) {
    this.state.highlight = []
    this.setState({
      highlight: this.state.highlight
    })

    let chosen = this.state.selected.indexOf(idx)
    if ( chosen < 0 ) {
      this.state.selected.push(idx)
    } else {
      this.state.selected.splice(chosen, 1);
    }
    if (this.props.type === 'single') {
      if (this.state.selected.length >= 2) {
        this.state.selected.shift()
      }
    } else if (this.props.type === 'triple') {
      if (this.state.selected.length >= 4) {
        this.state.selected.shift()
      }
    }
      
    for (let i = 0; i < this.state.selected.length; i++) {
      this.state.highlight[this.state.selected[i]] = 'highlight'
    }

    /*start to record user's slection order*/
    let cleanSelection = this.state.selected.filter(function(val) {
      return val !== null
    })

    let selction_list = []

    for (let i = 0; i < cleanSelection.length; i++) {
      //console.log(this.props.options[cleanSelection[i]].text)
      selction_list.push(this.props.options[cleanSelection[i]].text)
    }
    /*end recording user's slection order*/

    this.setState({
      highlight: this.state.highlight,
      selected: this.state.selected,
      currentQuestion: this.state.currentQuestion
    })
  },

  render: function () {
    return (
        <ul className='optionAll' >
          {this.props.options.map((option, idx) =>
              <li className={this.state.highlight[idx]}  key={option.text} width='400px' onClick={this.change.bind(this, idx, this.props.questionNumber)}>
                {String.fromCharCode(97 + idx).toUpperCase()}.
                <img className="optionIcon" src={option.image}/>
                &nbsp;&nbsp;{option.text}
              </li>
          )}
          <li className="input">{String.fromCharCode(97 + this.props.options.length).toUpperCase()}. 其他 <input type="text" name="other" value={this.state.value} onChange={this.inputChange}/></li>
        </ul>
      )
    }
  })

export default OptionText


