require('normalize.css');
require('styles/App.css');

import React from 'react'
//import PageContainer from './transitions'
import SocialBar from './SocialBar'
import memory from './Tracker'

let ResultCard = React.createClass({

	getInitialState: function () {
      return {
        valueEmail: '',
        valueWhatsapp: '',
        valueWechat: '',
        valueFacebook: '',
        submitWord: '提交'
      }
  },

  inputChange: function(event){
    if (event.target.name === 'email') {
	    this.setState({
	    	valueEmail: event.target.value
	    })
    } else if (event.target.name === 'whatsapp') {
    	this.setState({
	    	valueWhatsapp: event.target.value
	    })
    } else if (event.target.name === 'wechat') {
    	this.setState({
	    	valueWechat: event.target.value
	    })
    } else if (event.target.name === 'facebook') {
      this.setState({
        valueFacebook: event.target.value
      })
    } else if (event.target.name === 'submit') {
    	this.setState({
	    	submitWord: event.target.value
	    })
    }
  },

  submit: function(){
    let contactInfo = {"Email": this.state.valueEmail, "WhatsApp": this.state.valueWhatsapp, "Wechat": this.state.valueWechat, "Facebook": this.state.valueFacebook}
  	memory.sendMessage('initiumusersurvey_separate', 'ContactInfo', 'Email_' + this.state.valueEmail + ' | WhatsApp_' + this.state.valueWhatsapp + ' | Wechat_' + this.state.valueWechat + ' | Facebook_' + this.state.valueFacebook)
    memory.sendMessage('initiumusersurvey_whole', 'ContactInfo', JSON.stringify(contactInfo), '')

  	this.setState({
  		valueEmail: '',
  		valueWhatsapp: '',
  		valueWechat: '',
      valueFacebook: '',
  		submitWord: '提交成功！'
  	})
  },

  render: function () {
    return (
	  <div className='surface'>
	  	<h3 className="title">你是不是願意成為端的VIP讀者，參與我們的話題討論、甚至內容製作呢？願意的話，留一個聯絡方式好不好呀？（選填）</h3>
	  	<div className="contact">
			<span className="contactMet">Email</span><input type="text" name="email" value={this.state.valueEmail} onChange={this.inputChange}/>
			<br/><br/>
			<span className="contactMet">WhatsApp</span>
			<input type="text" name="whatsapp" value={this.state.valueWhatsapp} onChange={this.inputChange}/>
			<br/><br/>
			<span className="contactMet">WeChat</span>
			<input type="text" name="wechat" value={this.state.valueWechat} onChange={this.inputChange}/>
			<br/><br/>
      <span className="contactMet">Facebook</span>
      <input type="text" name="facebook" value={this.state.valueFacebook} onChange={this.inputChange}/>
      <br/><br/>
			<input id="submit" type="submit" name="submit" value={this.state.submitWord} onClick={this.submit}/>
		</div>
	  	<SocialBar memory={memory} />
	  </div>
	  )
	}
})

export default ResultCard


