/**
 * A tracker for customized event tracking
 */

/**
 * creates a tracker customized for Initium Lab backend
 * @param appLabel - the identifier of the app, to be sent to backend
 */
var apiPrefix = 'https://ss.initiumlab.com/';
var urlRemember = apiPrefix + 'remember/';
var urlUUID = apiPrefix + 'utility/uuid/';
var $ = require ('jquery')

var memory = ({

  getApiPrefix: function(callback){
    callback(apiPrefix);
  },
  requestUUID: function(callback){
    $.get(urlUUID).then(function(response){
      if (!localStorage.uuid) {
        localStorage.setItem('uuid', response.data.uuid);
      }
      if (callback) {
        callback(response);
      }
    })
    // , function(response){
    //   //console.log('Error:' + response);
    // });
  },
  getUUID: function(callback){
    if (!localStorage.uuid){
      memory.requestUUID(function(){
        callback(localStorage.uuid);
      });
    } else {
      callback(localStorage.uuid);
    }
  },
  sendMessage: function(eventname, key, value, raw, callback){
    var sendMessageFunction = function(){
      $.ajax(
        {
          url: urlRemember + eventname + '/',
          type: 'POST',
          //dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          async: true,
          data: JSON.stringify({
            username: localStorage.uuid,
            key: key,
            value: value,
            raw: raw
          }),
          success: function(response){
            //console.log(response);
            if (callback) {
              callback(response);
            }
          }
        }
      );
    };
    if (localStorage.uuid) {
      sendMessageFunction();
    } else {
      memory.requestUUID(sendMessageFunction);
    }
  }
})

export default memory


