require('normalize.css');
require('styles/App.css');

import React from 'react'
import QuestionText from './QuestionText'
import OptionText from './OptionText'

//import Transition from './transitions'


let ChoiceCard = ({currentQA, handleOptionClick, nextClickHandler, prevClickHandler, questionNumber, totalQuestionNumber, state, answers}) =>
  <div className={currentQA.layout}>
  	<div className='surface' id = {state}>
  		<QuestionText question={(questionNumber + 1) + '/' + totalQuestionNumber + ' ' + currentQA.question} />
  		<OptionText questionContent={currentQA.question} options={currentQA.options} handleOptionClick={handleOptionClick} questionNumber={questionNumber} type={currentQA.type} answers={answers} totalQuestionNumber={totalQuestionNumber}/>
  		<div className="allBar"></div>
  		<div className="partBar" style={{width: (questionNumber/totalQuestionNumber).toFixed(2)* 100 + '%'}}></div>
  	</div>
  	<div className="buttonArea">
			<div className="prevButton" onClick={prevClickHandler}>上一題</div>
			<div className="nextButton" onClick={nextClickHandler}>下一題</div>
		</div>
  </div>
export default ChoiceCard


